/* 수정 예정 */
.swipe-item {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%; 
    background-size: cover;
  
    /* animation: slider 50s infinite ; */
    /* animation: fade-in 2s; */
    /* animation-fill-mode: forwards; */
  }

  @keyframes fade-in {
    from {
      opacity: 0.5;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fade-out {
    from {
      opacity: 1;
  
    }
    to {
      opacity: 0.5;
    }
  } 
